.navLink {
    text-decoration: none;
    font-size: 16px;
    color: #4a586a;
    font-weight: 700;
    background: none;
    border: none;
}
.navLinkActive {
    composes: navLink;
    color: palevioletred;
}
.navList {
  display: flex;
  list-style: none;
  flex-direction: row;
  padding: 16px;
  border-bottom: 2px solid #9895a2;
  margin: 0;
  box-sizing: border-box;
}
.navList li {
    min-width: 100px;
}